"use client";

import Image from "next/image";
import { useState } from "react";

const ChevronDown = ({ className }) => (
  <svg
    width="12"
    height="12"
    viewBox="0 0 12 12"
    fill="none"
    xmlns="http://www.w3.org/2000/svg"
    className={className}
  >
    <path
      d="M3 4.5L6 7.5L9 4.5"
      stroke="currentColor"
      strokeWidth="1.5"
      strokeLinecap="round"
      strokeLinejoin="round"
    />
  </svg>
);

export default function Navbar() {
  const [language, setLanguage] = useState("EN");
  const [langDropdownOpen, setLangDropdownOpen] = useState(false);

  const navItems = [
    { label: "Services", hasDropdown: true },
    { label: "Insights", hasDropdown: true },
    { label: "Explore Manjam", hasDropdown: true },
  ];

  return (
    <nav className="w-full bg-white">
    
        <div className="flex items-center justify-between h-[80px]">
          {/* Left - Navigation Menu */}
          <div className="flex items-center gap-5 w-[336px] h-5">
            {navItems.map((item, index) => (
              <button
                key={index}
                className="flex items-center gap-1 text-[#1C4A3F] font-medium text-sm hover:opacity-80 transition-opacity whitespace-nowrap font-(family-name:--font-space-grotesk)"
              >
                <span>{item.label}</span>
                {item.hasDropdown && <ChevronDown className="text-[#1C4A3F]" />}
              </button>
            ))}
          </div>

          {/* Center - Logo */}
          <div className="absolute left-1/2 transform -translate-x-1/2">
            <Image
              src="/manjam-logo.png"
              alt="Manjam Logo"
              width={130.2}
              height={42}
              priority
              className="object-contain"
            />
          </div>

          {/* Right - Language Selector & Buttons */}
          <div className="flex items-center gap-4">
            {/* Language Selector */}
            <div className="relative">
              <button
                onClick={() => setLangDropdownOpen(!langDropdownOpen)}
                className="flex items-center gap-1 text-[#1C4A3F] font-medium text-sm hover:opacity-80 transition-opacity"
              >
                <span>{language}</span>
                <ChevronDown className="text-[#1C4A3F]" />
              </button>
              {langDropdownOpen && (
                <div className="absolute top-full mt-2 bg-white border border-[#E5E7EB] rounded-lg shadow-lg py-1 min-w-[60px] z-50">
                  <button
                    onClick={() => {
                      setLanguage("EN");
                      setLangDropdownOpen(false);
                    }}
                    className={`w-full px-3 py-2 text-left text-sm hover:bg-gray-50 ${
                      language === "EN"
                        ? "text-[#1C4A3F] font-medium"
                        : "text-gray-600"
                    }`}
                  >
                    EN
                  </button>
                  <button
                    onClick={() => {
                      setLanguage("AR");
                      setLangDropdownOpen(false);
                    }}
                    className={`w-full px-3 py-2 text-left text-sm hover:bg-gray-50 ${
                      language === "AR"
                        ? "text-[#1C4A3F] font-medium"
                        : "text-gray-600"
                    }`}
                  >
                    AR
                  </button>
                </div>
              )}
            </div>

            {/* Login Button - Outlined */}
            <button className="flex items-center justify-center w-[113px] h-[46px] py-[14px] px-5 gap-2 rounded-lg border border-[#1C4A3F] shadow-[0px_2px_6px_0px_rgba(16,24,40,0.06)] text-[#1C4A3F] font-medium text-sm transition-all hover:bg-[radial-gradient(50%_50%_at_50%_50%,#111827_0%,#153830_100%)] hover:text-white cursor-pointer">
              Log in
            </button>

            {/* Become a Client Button - Primary */}
            <button className="flex items-center justify-center w-[155px] h-[46px] py-[14px] px-5 gap-2 rounded-lg border border-[#1C4A3F] bg-[radial-gradient(50%_50%_at_50%_50%,#111827_0%,#153830_100%)] shadow-[0px_2px_6px_0px_rgba(16,24,40,0.06)] text-white font-medium text-sm transition-all hover:opacity-90 cursor-pointer">
              Become a Client
            </button>
          </div>
        </div>

        {/* Bottom border */}
        <div className="h-px bg-[#E5E7EB]"></div>
    </nav>
  );
}

